import axios from "axios";
console.log(process.env);
const TR7_BOT_API_ENDPOINT =
  process.env.NODE_ENV === "production"
    ? process.env.REACT_APP_API_ENDPOINT_STAGING
    : process.env.REACT_APP_API_ENDPOINT_LOCAL;

// axios.defaults.withCredentials = true;
axios.defaults.headers = {
  "Access-Control-Allow-Origin": "*",
  "Content-Type": "application/json",
};
const options = {
  method: "GET",
  mode: "cors",
  headers: {
    Accept: "application/json",
    "Content-Type": "application/json",
    "x-access-token": "",
  },
  // credentials: "include",
};

const getApiKeyList = async () => {
  var fetchUrl = `${TR7_BOT_API_ENDPOINT}/keylist`;
  const data = window
    .fetch(fetchUrl, options)
    .then((response) => response.json())
    .then((response) => {
      return response;
    })
    .catch((err) => console.error(err));
  const json = await data;

  return json;
};
const adminAuthenticate = async (apiKey, password) => {
  var fetchUrl = `${TR7_BOT_API_ENDPOINT}/login`;
  // const data = axios
  //   .post(
  //     fetchUrl,
  //     { apiKey, password },
  //     {
  //       withCredentials: false,
  //       headers: {
  //         // "Access-Control-Allow-Origin": "*",
  //         "Content-Type": "application/json",
  //       },
  //     }
  //   )
  //   .then((response) => {
  //     console.log(response);
  //     return true;
  //   })
  //   .catch((err) => false);
  // return await data;

  const tradeOption = { ...options };
  tradeOption.method = "POST";
  tradeOption.body = JSON.stringify({ apiKey, password });
  const data = window
    .fetch(fetchUrl, tradeOption)
    .then((response) => response.json())
    .then((response) => {
      return response;
    })
    .catch((err) => console.error(err));
  const json = await data;
  console.log(json);
  const { token } = json;
  console.log(token);
  if (token) {
    options.headers["x-access-token"] = token;
  }
  console.log("auth", options);
  return json;
};

